.stats-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 500px;
  height: 530px;
  max-height: 600px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.summary-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 212px;
  /*background-color: red;*/
  -webkit-box-shadow: 0 14px 45px rgba(0, 0, 0, 0.1), 0 10px 18px rgba(0, 0, 0, 0.1);
          box-shadow: 0 14px 45px rgba(0, 0, 0, 0.1), 0 10px 18px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.chart-summary-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 292px;
  margin-top: 26px;
  /*background-color: red;*/
  -webkit-box-shadow: 0 14px 45px rgba(0, 0, 0, 0.1), 0 10px 18px rgba(0, 0, 0, 0.1);
          box-shadow: 0 14px 45px rgba(0, 0, 0, 0.1), 0 10px 18px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.weekly-title {
  width: 60%;
  margin: 10px 20px;
  font-size: 45px;
  font-weight: lighter;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.totals {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0 15px;
  margin: 10px 0 60px;
}

.totals-cell {
  width: 33%;
  text-align: center;
}

.totals-cell-title {
  margin: 5px 0;
  color: #757575;
  font-size: 20px;
  font-weight: lighter;
}

.totals-cell-value {
  margin: 5px 0;
  color: #757575;
  font-size: 40px;
  font-weight: normal;
}



