  background-color: #FFF;
  z-index: 1000;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.dash-days-list {
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  height: 100%;
  /*background-color: red;*/
}



