@-webkit-keyframes moveUp {
  from { -webkit-transform: translateY(-200%); transform: translateY(-200%); }
  to   { -webkit-transform: translateY(0); transform: translateY(0);     }
}

@keyframes moveUp {
  from { -webkit-transform: translateY(-200%); transform: translateY(-200%); }
  to   { -webkit-transform: translateY(0); transform: translateY(0);     }
}

.activities-wrapper {
  width: 50%;
  max-width: 580px;
  height: 530px;
  /*max-height: 600px;*/
  overflow: hidden;
  -webkit-box-shadow: 0 14px 45px rgba(0, 0, 0, 0.1), 0 10px 18px rgba(0, 0, 0, 0.1);
          box-shadow: 0 14px 45px rgba(0, 0, 0, 0.1), 0 10px 18px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.activities-list {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  height: 80%;
  -webkit-animation: moveUp 0.7s;
          animation: moveUp 0.7s;
  z-index: 0;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}



