@-webkit-keyframes login-appear {
  from { -webkit-transform: scaleY(0); transform: scaleY(0); }
  to   { -webkit-transform: scaleY(1); transform: scaleY(1); }
}

@keyframes login-appear {
  from { -webkit-transform: scaleY(0); transform: scaleY(0); }
  to   { -webkit-transform: scaleY(1); transform: scaleY(1); }
}

@-webkit-keyframes login-fail {
  0%   { -webkit-transform: translateX(0); transform: translateX(0); }
  25%  { -webkit-transform: translateX(-5%); transform: translateX(-5%); }
  50%  { -webkit-transform: translateX(5%); transform: translateX(5%); }
  75%  { -webkit-transform: translateX(-5%); transform: translateX(-5%); }
  100% { -webkit-transform: translateX(0); transform: translateX(0); }
}

@keyframes login-fail {
  0%   { -webkit-transform: translateX(0); transform: translateX(0); }
  25%  { -webkit-transform: translateX(-5%); transform: translateX(-5%); }
  50%  { -webkit-transform: translateX(5%); transform: translateX(5%); }
  75%  { -webkit-transform: translateX(-5%); transform: translateX(-5%); }
  100% { -webkit-transform: translateX(0); transform: translateX(0); }
}

.login-form {
  position: relative;
  display: grid;
  grid-template-rows: 80px 1fr 40px;
  width: 85%;
  max-width: 450px;
  min-width: 450px;
  padding: 20px;
  -webkit-animation: login-appear 0.2s;
          animation: login-appear 0.2s;
  background-color: #FFF;
  border-radius: 10px;
  -webkit-box-shadow: 0 14px 45px rgba(0, 0, 0, 0.2), 0 10px 18px rgba(0, 0, 0, 0.2);
          box-shadow: 0 14px 45px rgba(0, 0, 0, 0.2), 0 10px 18px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  z-index: 1000;
}

.input-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.login-submit-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 80%;
  margin: 0 auto;
}

.user-fail-anim {
  display: grid;
  grid-template-rows: 80px 1fr 40px;
  width: 85%;
  max-width: 450px;
  min-width: 450px;
  padding: 20px;
  -webkit-animation: login-fail 0.2s;
          animation: login-fail 0.2s;
  background-color: #FFF;
  -webkit-box-shadow: 0 14px 45px rgba(0, 0, 0, 0.2), 0 10px 18px rgba(0, 0, 0, 0.2);
          box-shadow: 0 14px 45px rgba(0, 0, 0, 0.2), 0 10px 18px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  z-index: 1000;
}



