@-webkit-keyframes banner-anim {
  0% {
    z-index: 9000;
  }
  100% {
    -webkit-transform: scale(10);
            transform: scale(10);
    z-index: 9000;
  }
}

@keyframes banner-anim {
  0% {
    z-index: 9000;
  }
  100% {
    -webkit-transform: scale(10);
            transform: scale(10);
    z-index: 9000;
  }
}

.welcome-container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.welcome-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 0 40px;
}

.welcome-nav > * {
  position: relative;
  margin: 20px 10px;
  color: #757575;
  font-weight: bold;
  text-decoration: none;
  z-index: 1000;
}

.tab-active {
  color: #3F51B5;
}

.welcome-main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  /*background-color: blue;*/
  height: 100%;
}

.welcome-login-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  width: 70%;
  padding-right: 100px;
  /*background-color: red;*/
}

.banner {
  position: relative;
  /*left: -20%;*/
  top: 0;
  left: 0;
  width: 500px;
  height: 500px;
  background-color: #3F51B5;
  border-radius: 50%;
  -webkit-transform: scale(2);
      -ms-transform: scale(2);
          transform: scale(2);
  z-index: 1000;
}

.banner-anim .banner {
  -webkit-animation: banner-anim 0.5s cubic-bezier(.75,-0.53,.83,.67);
          animation: banner-anim 0.5s cubic-bezier(.75,-0.53,.83,.67);
}

.demo-pic-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
  display: block;
  width: 600px;
  min-width: 450px;
  padding: 5px;
  background-color: #FFF;
  -webkit-box-shadow: 0 14px 45px rgba(0, 0, 0, 0.2), 0 10px 18px rgba(0, 0, 0, 0.2);
          box-shadow: 0 14px 45px rgba(0, 0, 0, 0.2), 0 10px 18px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  border-radius: 10px;
  -webkit-animation: login-appear 0.2s;
          animation: login-appear 0.2s;
  overflow: hidden;
}

.demo-pic {
  display: block;
  width: 95%;
  min-width: 450px;
  margin: 30px auto;
}

.welcome-msg {
  position: absolute;
  left: 25px;
  top: 35%;
  width: 550px;
  /*max-width: 400px;*/
  /*min-width: 400px;*/
  z-index: 2000;
  /*background-color: orange;*/
}

.welcome-title {
  margin: 10px 20px 20px;
  color: #FFF;
  font-size: 60px;
}

.welcome-body {
  margin: 10px 20px 20px;
  color: #FFF;
  letter-spacing: 2px;
  line-height: 30px;
}



